<template>
  <div id="view-project">
    <h2>{{project_name}}</h2>
    <h4>{{organisation}}({{year}})</h4>
    <div class="container">
      <b-badge pill variant="light">{{flag}}</b-badge>
      <b-badge pill variant="light">{{employment}}</b-badge>

      <div id="project-skills">
        <h5>Skills Involved</h5>
        <ul>
          <li v-for="elem in category" :key="elem">
            <div>{{elem}}</div>
          </li>
        </ul>
      </div>

      <div v-for="(elem, idx) in description" v-bind:key="idx">
        <p>{{elem}}</p>
      </div>
    </div>

  </div>
</template>

<style>
  .container {
    margin-top: 3%;
  }
  div#view-project {
    height: 700px;
  }
  div#project-skills {
    margin-bottom: 4%;
  }
</style>

<script>
  import db from '../assets/firebaseInit'

  export default {
    name: 'view-project',
    data () {
      return {
        project_id: null,
        project_name: null,
        organisation: null,
        year: null,
        flag: null,
        employment: null,
        description: null,
        category: null,
        project_arr: [],
        flag_project_arr: [],
        loading: true
      }
    },
    beforeRouteEnter (to, from, next) {
      db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('projects').where('project_id', '==', to.params.project_id).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.project_id = doc.data().project_id
            vm.project_name = doc.data().project_name
            vm.organisation = doc.data().organisation
            vm.year = doc.data().year
            vm.flag = doc.data().flag
            vm.employment = doc.data().employment
            vm.description = doc.data().description
            vm.category = doc.data().category

            if (vm.organisation == "") {
              vm.organisation = null
            }
            else{
              vm.organisation = 'by '+vm.organisation+' '
            }
            if (vm.flag) {
              vm.flag = "non-academic"
            }
            else {
              vm.flag = "academic"
            }
            
            if (vm.employment == 1) {
              vm.employment = "part-time"
            }
            else if (vm.employment == 2) {
              vm.employment = "full-time"
            }
            else {
              vm.employment = "project-based"
            }
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('projects').where('project_id', '==', this.$route.params.project_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.project_id = doc.data().project_id
            this.project_name = doc.data().project_name
            this.organisation = doc.data().organisation
            this.year = doc.data().year
            this.flag = doc.data().flag
            this.employment = doc.data().employment
            this.description = doc.data().description
            this.category = doc.data().category
          })
          if (this.flag) {
            this.flag = "non-academic"
          }
          else {
            this.flag = "academic"
          }
          
          if (this.employment == 1) {
            this.employment = "part-time"
          }
          else if (this.employment == 2) {
            this.employment = "full-time"
          }
          else {
            this.employment = "project-based"
          }
        })
      }
    }
  }
</script>


