<template>
  <div id="viewdata">
    <h2>{{title}}</h2>
    <div class="container" v-for="(blog, index) in blog_arr" :key="index">
      <!-- subsection -->
      <div v-if="blog[0]">
        <h4>{{blog[0]}}</h4>
      </div>

      <!-- subsubsection -->
      <div v-if="blog[1]">
        <h5>{{blog[1]}}</h5>
      </div>


      <!-- paragraphs -->
      <div v-if="blog[3]">
        <p>{{blog[3]}}</p>
      </div>

      <!-- images -->
      <div v-if="blog[2]">
        <img :src="getImgUrl(blog[2])" v-bind:alt="blog[2]">
      </div>
    </div>

    <div class="container" v-if="links">
      <h5>Links to Check Out</h5>
      <ul>
        <li v-for="link in links" :key="link">
          <div>{{link}}</div>
        </li>
      </ul>
    </div>

  </div>
</template>

<style>
  .container {
    margin-top: 3%;
  }
</style>

<script>
  import db from '../assets/firebaseInit'
  import Vue from 'vue'
  import { CardPlugin } from 'bootstrap-vue'
  Vue.use(CardPlugin)

  export default {
    name: 'viewdata',
    data () {
      return {
        title: null,
        paragraphs: null,
        subsections: null,
        subsubsections: null,
        imgs: null,
        links: null,
        blog_size: 0,
        blog_arr: [],
        loading: true
      }
    },
    beforeRouteEnter (to, from, next) {
      db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('blogs').where('blog_id', '==', to.params.blog_id).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.title = doc.data().title
            vm.paragraphs = doc.data().paragraphs
            vm.subsections = doc.data().subsections
            vm.subsubsections = doc.data().subsubsections
            vm.imgs = doc.data().imgs
            vm.links = doc.data().links
            vm.blog_size = doc.data().blog_size
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('blogs').where('blog_id', '==', this.$route.params.blog_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.title = doc.data().title
            this.paragraphs = doc.data().paragraphs
            this.subsections = doc.data().subsections
            this.subsubsections = doc.data().subsubsections
            this.imgs = doc.data().imgs
            this.links = doc.data().links
            this.blog_size = doc.data().blog_size
          })
        })
      },
      sort_arr () {
        var blog_arr = []
        console.log(this.blog_size)
        for (let i = 0; i < this.blog_size; i++){
          if (this.subsections[i] == "") {
            this.subsections[i] = null
          }
          if (this.subsubsections[i] == "") {
            this.subsubsections[i] = null
          }
          if (this.imgs[i] == "") {
            this.imgs[i] = null
          }
          if (this.paragraphs[i] == "") {
            this.paragraphs[i] = null
          }
          blog_arr[i] = [this.subsections[i], this.subsubsections[i], this.imgs[i], this.paragraphs[i]]
        }
        return blog_arr
      },
      getImgUrl(pic) {
        return require('../images/blog'+this.$route.params.blog_id+'/img'+pic+'.jpg')
      }
    },
    mounted: function () {
      this.blog_arr = this.sort_arr();
    }
  }
</script>


