<template>
  <div id="business">
    <h3>Business and Pricing</h3>
    <div div v-for="business in businesses" v-bind:key="business.id">
      <b-card
        :title=business.product
        img-src="https://picsum.photos/600/300/?image=25"
        img-alt="Image"
        img-top
        tag="article"
        style="max-width: 20rem;"
        class="mb-2"
      >
        <b-card-text>{{business.description}}</b-card-text>

        <b-button href="#" variant="primary">See Pricings</b-button>
      </b-card>
    </div>
  </div>
</template>

<script>
  import db from '../assets/firebaseInit'
  import Vue from 'vue'
  import { CardPlugin } from 'bootstrap-vue'
  Vue.use(CardPlugin)

  export default {
    name: 'business',
    data () {
      return {
        businesses: [],
        loading: true,
        doc_id: 'RAVqGWKerLwJRVJyBVXf'
      }
    },
    created () {
      db.collection('personal_details').doc(this.doc_id).collection('business').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'product': doc.data().product,
            'description': doc.data().description,
            'note': doc.data().note,
            'prices': doc.data().prices
          }
          this.businesses.push(data)
        })
      })
    }
  }
</script>


