<template>
  <div id="blog" class="accordion" role="tablist">
    <h3>Blog</h3>

    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-1 variant="info">{{blog_arr[0].title}}</b-button>
      </b-card-header>
      <b-collapse id="accordion-1" visible accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text>{{blog_arr[0].description}}</b-card-text>
          <router-link v-bind:to="{ name: 'ViewBlog', params: { blog_id: blog_arr[0].blog_id }}">
            <b-button>Read more...</b-button>
          </router-link>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-2 variant="info">{{blog_arr[1].title}}</b-button>
      </b-card-header>
      <b-collapse id="accordion-2" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text>{{blog_arr[1].description}}</b-card-text>
          <router-link v-bind:to="{ name: 'ViewBlog', params: { blog_id: blog_arr[1].blog_id }}">
            <b-button>Read more...</b-button>
          </router-link>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-3 variant="info">{{blog_arr[2].title}}</b-button>
      </b-card-header>
      <b-collapse id="accordion-3" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text>{{blog_arr[2].description}}</b-card-text>
          <router-link v-bind:to="{ name: 'ViewBlog', params: { blog_id: blog_arr[2].blog_id }}">
            <b-button>Read more...</b-button>
          </router-link>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-4 variant="info">{{blog_arr[3].title}}</b-button>
      </b-card-header>
      <b-collapse id="accordion-4" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text>{{blog_arr[3].description}}</b-card-text>
          <router-link v-bind:to="{ name: 'ViewBlog', params: { blog_id: blog_arr[3].blog_id }}">
            <b-button>Read more...</b-button>
          </router-link>
        </b-card-body>
      </b-collapse>
    </b-card>


  </div>
</template>

<style>
  #blog {
    margin-top: 2%;
    padding-bottom: 53%;
  }
  button.btn.btn-info.btn-block.not-collapsed, button.btn.btn-info.btn-block.collapsed {
    background-color: #9e8d64;
    border-color: #9e8d64;
  }
</style>

<script>
  import db from '../assets/firebaseInit'

  export default {
    name: 'blog',
    data () {
      return {
        blog_arr: [],
        loading: true,
        doc_id: 'RAVqGWKerLwJRVJyBVXf'
      }
    },
    created () {
      db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('blogs').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'blog_id': doc.data().blog_id,
            'title': doc.data().title,
            'description': doc.data().description
          }
          this.blog_arr.push(data)
          // console.log(this.blog_arr)
        })
      })
    }
  }
</script>


