<template>
  <div id="footer-sec">
    <b-container class="bv-example-row">
      <b-row>
        <b-col cols="10">
        </b-col>
        <b-col cols="2" id="connect">
          <div v-for="info in contact_info" v-bind:key="info.id">
            <a href="https://uk.linkedin.com/in/jia-mei-grace-lim-0a17a0193?trk=profile-badge" class="fa fa-linkedin-square"></a>
            <a href="https://www.facebook.com/jiamei.pisces/" class="fa fa-facebook-official"></a>
            <a href="https://github.com/ljmgrace" class="fa fa-github"></a>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="7" id="license">Copyright (c) 2021 ljmgrace - MIT License</b-col>
      </b-row>
    </b-container>
  </div>
</template>

<style>
  div#connect {
    font-size: 2.5em;
  }
  div#connect > div > a.fa {
    padding: 4%;
    margin-top: 8%;
    color: #FFFFFF80;
    text-decoration: none;
  }
  #license {
    color: #FFFFFF80;
    margin-bottom: 1%;
  }
  .bv-example-row.container{
    position: relative;
    bottom: 0;
    background-color: #82714a;
    min-width: 100%;
  }

  .bv-example-row.container > .row{
    margin-bottom: 0px;
  }
</style>

<script>
  import db from '../assets/firebaseInit'
  import Vue from 'vue'
  import { LayoutPlugin } from 'bootstrap-vue'
  Vue.use(LayoutPlugin)

  export default {
    name: 'footer-sec',
    data () {
      return {
        contact_info: [],
        loading: true,
        doc_id: 'RAVqGWKerLwJRVJyBVXf'
      }
    },
    created () {
      db.collection('personal_details').doc(this.doc_id).collection('contacts').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'email_address': doc.data().email_address,
            'github': doc.data().github,
            'linkedin': doc.data().linkedin,
            'social_media': doc.data().social_media,
            'phone': doc.data().phone
          }
          this.contact_info.push(data)
        })
      })
    }
  }
</script>


