<template>
  <div id="pricing" class="accordion" role="tablist">
    <h3>Pricing</h3>
    <div>
      <b-card
        title="Portfolio Website"
        img-src="https://picsum.photos/600/300/?image=25"
        img-alt="Image"
        img-top
        tag="article"
        style="max-width: 20rem;"
        class="mb-2"
      >
        <b-card-text>
          Some quick example text to build on the card title and make up the bulk of the card's content.
        </b-card-text>

        <b-button href="#" variant="primary">See Pricings</b-button>
      </b-card>
    </div>
    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-1 variant="info">Accordion 1</b-button>
      </b-card-header>
      <b-collapse id="accordion-1" visible accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text>I start opened because <code>visible</code> is <code>true</code></b-card-text>
          <b-card-text>{{ text }}</b-card-text>
        </b-card-body>
      </b-collapse>
    </b-card>

    <!-- <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-2 variant="info">Accordion 2</b-button>
      </b-card-header>
      <b-collapse id="accordion-2" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text>{{ text }}</b-card-text>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-3 variant="info">Accordion 3</b-button>
      </b-card-header>
      <b-collapse id="accordion-3" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <b-card-text>{{ text }}</b-card-text>
        </b-card-body>
      </b-collapse>
    </b-card> -->
  </div>
</template>

<script>
  import db from '../assets/firebaseInit'
  // import Vue from 'vue'
  // import { CollapsePlugin } from 'bootstrap-vue'
  // Vue.use(CollapsePlugin)

  export default {
    name: 'pricing',
    data () {
      return {
        contact_info: [],
        loading: true,
        doc_id: 'RAVqGWKerLwJRVJyBVXf'
      }
    },
    created () {
      db.collection('personal_details').doc(this.doc_id).collection('business').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'product': doc.data().product,
            'description': doc.data().description,
            'note': doc.data().note,
            'prices': doc.data().prices
          }
          this.contact_info.push(data)
        })
      })
    }
  }
</script>


