<template>
  <div id="about">
    <h2>About</h2>
    <div class="container" v-for="(des, idx) in description_arr[0]" :key="idx">
      <p>{{des}}</p>
    </div>
  </div>
</template>

<style>
  #about {
    margin-top: 2%;
  }
</style>

<script>
  import db from '../../assets/firebaseInit'
  export default {
    name: 'about',
    data () {
      return {
        description_arr: [],
        loading: true
      }
    },
    created () {
      var arr = []
      db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('about').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'description': doc.data().description
          }
          this.description_arr.push(data.description)
        })
      })
    }
  }
</script>


