<template>
  <div id="project">
    <h3>Projects</h3>
    <div class="card-proj">
      <h4>Academic</h4>
      <b-card-group deck v-for="project in project_academic_arr" v-bind:key="project.id">
        <b-card :title=project.project_name :img-src="getImgUrl(project.project_id)" img-alt="Card image" img-top>
        <router-link v-bind:to="{ name: 'ViewProject', params: { project_id: project.project_id }}" >
          <b-button>Read more...</b-button>
        </router-link>
          <!-- <b-card-text>
            <p>{{project.organisation}} ({{project.year}}) [{{project.employment}}]</p>
            {{project.description}}
          </b-card-text> -->
          <!-- <b-button href="#" variant="primary">See Pricings</b-button> -->
        </b-card>
      </b-card-group>
    </div>
    <div class="card-proj">
      <h4>Non-Academic</h4>
      <b-card-group deck v-for="project in project_nonacademic_arr" v-bind:key="project.id">
        <b-card :title=project.project_name :img-src="getImgUrl(project.project_id)" img-alt="Card image" img-top>
        <router-link v-bind:to="{ name: 'ViewProject', params: { project_id: project.project_id }}" >
          <b-button>Read more...</b-button>
        </router-link>
          <!-- <b-card-text>
            <p>{{project.organisation}} ({{project.year}}) [{{project.employment}}]</p>
            {{project.description}}
          </b-card-text> -->
          <!-- <b-button href="#" variant="primary">See Pricings</b-button> -->
        </b-card>
      </b-card-group>
    </div>
  </div>
</template>

<style>
  #project {
    margin-top: 2%;
  }
  .card-deck {
    display: table-cell;
    width: 350px;
    /* height: 20%; */
  }
  .card-deck>.card>.card-body {
    background-color: #363636;
  }
  h4.card-title {
    min-height: 85px;
    font-weight: 500 !important;
  }
  .card-proj {
    margin-bottom: 5%;
  }
</style>

<script>
  import db from '../assets/firebaseInit'
  import Vue from 'vue'
  import { CardPlugin } from 'bootstrap-vue'
  Vue.use(CardPlugin)
  import { ButtonPlugin } from 'bootstrap-vue'
  Vue.use(ButtonPlugin)

  export default {
    name: 'project',
    data () {
      return {
        project_id: null,
        project_academic_arr: [],
        project_nonacademic_arr: [],
        flag_employment: [],
        flag_project: [],
        loading: true,
        doc_id: 'RAVqGWKerLwJRVJyBVXf'
      }
    },
    created () {
      db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('projects').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'project_id': doc.data().project_id,
            'project_name': doc.data().project_name,
            'description': doc.data().description,
            'employment': doc.data().employment,
            'flag': doc.data().flag
          }
          if (data.flag == 1) {
            this.project_nonacademic_arr.push(data)
            // console.log(this.project_nonacademic_arr)
          }
          else{
            this.project_academic_arr.push(data)
            // console.log(this.project_academic_arr)
          }
        })
      })
    },
    methods: {
      getImgUrl(pic) {
        return require('../images/projects/img'+pic+'.jpg')
      }
    }
  }
</script>


