<template>
    <div id="coming">
        <h4>This section is coming soon! Stay Tunned!</h4>
        <h5>Thank you for your patience...</h5>
        <div>
            <flip-countdown deadline="2021-08-01 00:00:00"></flip-countdown>
        </div>
    </div>
</template>

<style>
    #coming {
        margin-bottom: 58%;
    }
</style>
<script>
import FlipCountdown from 'vue2-flip-countdown'
export default {
    components: { FlipCountdown },
    name: 'coming',
    data () {
        return {}
    }
}
</script>



