<template>
    <div id="contact">
        <h3>Contact Details</h3>
        <div v-for="info in contact_info" v-bind:key="info.id">
            <p><i class="fa fa-envelope"></i> Email: {{info.email_address}}</p>
            <p><i class="fa fa-linkedin-square"></i> Linkedin: {{info.linkedin}}</p>
            <p><i class="fa fa-github"></i> Github: {{info.github}}</p>
            <p><i class="fa fa-whatsapp"></i> Whatsapp: {{info.phone[0]}}, {{info.phone[1]}}</p>
            <p><i class="fa fa-phone"></i> Mobile: (UK) {{info.phone[0]}}, (MY) {{info.phone[1]}}</p>
            <p><i class="fa fa-facebook-official"></i> Facebook: {{info.social_media.facebook}}</p>
            <p><i class="fa fa-wechat"></i> Wechat: {{info.social_media.wechat}}</p>
        </div>
        <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSfUg36HqmeSehTF4i7MSBNHM4aCsECPooJZ4RLzOpiAuXJdlQ/viewform?embedded=true" width="640" height="1000" frameborder="0" marginheight="0" marginwidth="0">Loading</iframe>
    </div>
</template>

<style>
    #contact {
        margin-top: 2%;
    }

    iframe {
        /* background-color: transparent; */
        display: block;
        margin: auto;
        margin-top: 10%;
    }
</style>

<script>
    import db from '../assets/firebaseInit'
    export default {
        name: 'contact',
        data () {
            return {
                contact_info: [],
                loading: true,
                doc_id: 'RAVqGWKerLwJRVJyBVXf'
            }
        },
        created () {
            db.collection('personal_details').doc(this.doc_id).collection('contacts').get().then((querySnapshot) => {
                this.loading = false
                querySnapshot.forEach((doc) => {
                    const data = {
                        'id': doc.id,
                        'email_address': doc.data().email_address,
                        'github': doc.data().github,
                        'linkedin': doc.data().linkedin,
                        'social_media': doc.data().social_media,
                        'phone': doc.data().phone
                    }
                    this.contact_info.push(data)
                })
            })
        }
    }
</script>


